import React, { useEffect, useState } from 'react';
import { useJoinParty, useSelectedPartyDetail } from '../../../../hooks/useParties';
import { TPartyFormFlow, TUserGameProfile } from '../../../../types/party';
import { TextField, Typography } from '@mui/material';
import {
	TextButton,
	FormCommonButton,
	FormContainer,
	FormDialogActions,
	FormDialogContent,
	FormDialogTitle,
} from '../../../../styles/pages/party/forms/Form.styles';
import UserGameProfileSelect from '../../UserGameProfileSelect';
import { useUser } from '../../../../hooks/useUsers';

type TPartyJoinFlowProps = {
	onFlowComplete: () => void;
	partyId: number;
	isPrivate: boolean;
};

export default function PartyJoinFlow({ onFlowComplete, partyId, isPrivate }: TPartyJoinFlowProps) {
	const { data, isLoading, isError, error } = useSelectedPartyDetail(partyId);
	const [formAccessCode, setFormAccessCode] = useState<string>('');
	const [selectedGameProfile, setSelectedGameProfile] = useState<TUserGameProfile | null>(null);

	const { data: user } = useUser();
	const { mutate: joinParty, isSuccess: isJoinSuccess, isError: isJoinError } = useJoinParty();
	//    
	const [view, setView] = useState<TPartyFormFlow>('form');
	useEffect(() => {
		if (isJoinSuccess) {
			setView('success');
		} else if (isJoinError) {
			setView('failed');
		}
	}, [isJoinSuccess, isJoinError, setView]);

	if (isLoading) return <div>  ...</div>;
	if (isError) return <div>   : {error.message} </div>;

	const handleOnJoinClick = () => {
		joinParty({
			partyId: partyId,
			gameUsername: selectedGameProfile?.gameUsername ?? '',
			profileId: selectedGameProfile?.id ?? undefined,
			accessCode: formAccessCode ?? '',
		});
	};

	// userId,
	// gameId,
	// setGameProfile,
	// validate,
	switch (view) {
		case 'form':
			return (
				<FormContainer>
					<FormDialogTitle> </FormDialogTitle>
					<FormDialogContent>
						<Typography>{`${data?.title} .`}</Typography>
						{isPrivate ? (
							<TextField
								value={formAccessCode}
								onChange={(e) => setFormAccessCode(e.target.value)}
								label=' '
								placeholder='  '
								required
							/>
						) : null}
						{user && user.id ? (
							<UserGameProfileSelect
								userId={user.id}
								gameId={data?.gameId}
								setGameProfile={setSelectedGameProfile}
								validate={''}
							/>
						) : (
							<Typography>  .</Typography>
						)}
					</FormDialogContent>
					<FormDialogActions>
						<TextButton onClick={onFlowComplete}></TextButton>
						<FormCommonButton
							onClick={handleOnJoinClick}
							variant='contained'
							disabled={false}
						>
							{''}
						</FormCommonButton>
					</FormDialogActions>
				</FormContainer>
			);
		case 'success':
			return (
				<FormContainer>
					<FormDialogTitle>  </FormDialogTitle>
					<FormDialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${selectedGameProfile?.gameUsername}!!`}
						</Typography>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${data?.title}   !`}
						</Typography>
					</FormDialogContent>
					<FormDialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</FormDialogActions>
				</FormContainer>
			);
		case 'failed':
			return (
				<FormContainer>
					<FormDialogTitle>  </FormDialogTitle>
					<FormDialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${selectedGameProfile?.gameUsername}!!`}
						</Typography>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${data?.title}   .`}
						</Typography>
					</FormDialogContent>
					<FormDialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</FormDialogActions>
				</FormContainer>
			);
		default:
			return (
				<FormContainer>
					<FormDialogTitle>  </FormDialogTitle>
					<FormDialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${selectedGameProfile?.gameUsername}!`}
						</Typography>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`     .  .`}
						</Typography>
					</FormDialogContent>
					<FormDialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</FormDialogActions>
				</FormContainer>
			);
	}
}
