import React, { useEffect, useState } from 'react';
import { TPartyFormFlow } from '../../../../types/party';
import { useCompleteParty } from '../../../../hooks/useParties';
import { Button } from '@mui/material';
import { DialogActions, DialogContent, DialogTitle, Typography } from '@mui/material';
import { TextButton, FormCommonButton } from '../../../../styles/pages/party/forms/Form.styles';

type TPartyCompleteFlowProps = {
	onFlowComplete: () => void;
	partyId: number;
};

export default function PartyCompleteFlow({ onFlowComplete, partyId }: TPartyCompleteFlowProps) {
	const [view, setView] = useState<TPartyFormFlow>('form');
	const { mutate: completeParty, isSuccess, isError } = useCompleteParty(partyId);

	useEffect(() => {
		if (isSuccess) {
			setView('success');
		} else if (isError) {
			setView('failed');
		}
	}, [isSuccess, isError]);
	const handleOnClick = () => {
		completeParty(partyId);
	};

	switch (view) {
		case 'form':
			return (
				<>
					<DialogTitle>  </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`  ?`}
						</Typography>
					</DialogContent>
					<DialogActions>
						<TextButton onClick={onFlowComplete}></TextButton>
						<FormCommonButton
							onClick={handleOnClick}
							variant='contained'
							disabled={false}
						>
							{' '}
						</FormCommonButton>
					</DialogActions>
				</>
			);
		case 'success':
			return (
				<>
					<DialogTitle>  </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`  .`}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<Button onClick={onFlowComplete} variant='contained' autoFocus>
							
						</Button>
					</DialogActions>
				</>
			);
		case 'failed':
			return (
				<>
					<DialogTitle>  </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`   .  .`}
						</Typography>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							   .
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<Button onClick={onFlowComplete} variant='contained' autoFocus>
							
						</Button>
					</DialogActions>
				</>
			);
	}
}
