import axios, { AxiosError, InternalAxiosRequestConfig } from 'axios';
import { loginSuccess, logout } from '../stores/authSlice';
import { refreshTokenApi } from './auth';
import { store } from '../stores';

const api = axios.create({
	baseURL: 'https://nas.stann.kr:4545/api', // 
	headers: {
		'Content-Type': 'application/json',
	},
	withCredentials: true, //    
});

const noAuthPaths = ['/auth/login', '/auth/register'];

api.interceptors.request.use((config) => {
	//   API   
	const isPublic = noAuthPaths.some((path) => config.url?.includes(path));
	if (!isPublic) {
		const token = localStorage.getItem('accessToken');
		if (token) {
			config.headers.Authorization = `Bearer ${token}`;
		}
	}
	return config;
});

let isRefreshing = false;
//         
let failedQueue: Array<{
	resolve: (value?: any) => void;
	reject: (reason?: any) => void;
}> = [];

/**
 *     
 * @param token   accessToken
 */
const processQueue = (token: string | null = null) => {
	failedQueue.forEach((prom) => {
		if (token) {
			prom.resolve(token);
		} else {
			prom.reject('Refresh token failed');
		}
	});
	failedQueue = [];
};

//   
const MAX_RETRY_COUNT = 3;
//  : accessToken  
api.interceptors.response.use(
	(res) => res,
	async (err: AxiosError) => {
		const originalRequest = err.config as InternalAxiosRequestConfig & {
			_retry?: boolean;
			_retryCount?: number;
		};

		const pathname = new URL(
			originalRequest.url!,
			originalRequest.baseURL || api.defaults.baseURL!
		).pathname;

		if (noAuthPaths.includes(pathname)) {
			return Promise.reject(err);
		}

		// 401  ,     (  )
		if (err.response?.status !== 401) {
			return Promise.reject(err);
		}

		originalRequest._retryCount = (originalRequest._retryCount || 0) + 1;
		originalRequest._retry = true; //   

		if (originalRequest._retryCount >= MAX_RETRY_COUNT) {
			console.warn('    ,  ');
			store.dispatch(logout());
			window.location.href = '/';
			return Promise.reject(err);
		}

		//     ,     
		if (isRefreshing) {
			return new Promise((resolve, reject) => {
				failedQueue.push({ resolve, reject });
			})
				.then((token) => {
					originalRequest.headers.Authorization = `Bearer ${token}`;
					return api(originalRequest);
				})
				.catch((refreshError) => {
					return Promise.reject(refreshError);
				});
		}

		//   
		isRefreshing = true;

		try {
			const response = await refreshTokenApi(); //   API 
			const newAccessToken = response.accessToken;

			store.dispatch(loginSuccess(newAccessToken));
			api.defaults.headers.common['Authorization'] = `Bearer ${newAccessToken}`;

			//       
			processQueue(newAccessToken);

			originalRequest.headers.Authorization = `Bearer ${newAccessToken}`;
			return api(originalRequest);
		} catch (refreshError) {
			console.error('refreshToken  ', refreshError);
			processQueue(null);
			store.dispatch(logout());
			window.location.href = '/';
			return Promise.reject(refreshError);
		} finally {
			isRefreshing = false; //   (/ )
			console.log('[DEBUG] isRefreshing  false ');
		}
	}
);
export default api;
