import React, { useEffect, useState } from 'react';
import { TPartyFormFlow } from '../../../../types/party';
import { Box, Button, DialogActions, DialogContent, DialogTitle, Typography } from '@mui/material';
import { useChangePartyLeader } from '../../../../hooks/useParties';
import { FormCommonButton, TextButton } from '../../../../styles/pages/party/forms/Form.styles';

type TPartyJoinFlowProps = {
	onFlowComplete: () => void;
	partyId: number;
	userId: number;
	userName: string;
};
export default function LeaderChangeFlow({
	onFlowComplete,
	partyId,
	userId,
	userName,
}: TPartyJoinFlowProps) {
	const [view, setView] = useState<TPartyFormFlow>('form');
	const {
		mutate: changePartyLeader,
		isSuccess,
		isError,
	} = useChangePartyLeader({ partyId, userId });
	useEffect(() => {
		if (isSuccess) {
			setView('success');
		} else if (isError) {
			setView('failed');
		}
	}, [isSuccess, isError]);
	const handleOnClick = () => {
		changePartyLeader({ partyId, userId });
	};

	switch (view) {
		case 'form':
			return (
				<>
					<DialogTitle>  </DialogTitle>
					<DialogContent sx={{ paddingBottom: 0 }}>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							<span style={{ fontWeight: 'bold' }}>{userName}</span> 
							?
						</Typography>
						<Box>
							<div> </div>
							<Typography
								sx={{ fontWeight: 'bold', fontSize: '18px' }}
							>{`${userName}`}</Typography>
						</Box>
					</DialogContent>
					<DialogActions>
						<TextButton onClick={onFlowComplete}></TextButton>
						<FormCommonButton
							onClick={handleOnClick}
							variant='contained'
							disabled={false}
						>
							{' '}
						</FormCommonButton>
					</DialogActions>
				</>
			);
		case 'success':
			return (
				<>
					<DialogTitle>  </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${userName}  .`}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</DialogActions>
				</>
			);
		case 'failed':
			return (
				<>
					<DialogTitle>  </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${userName}   .`}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</DialogActions>
				</>
			);
	}
}
