import React, { useEffect, useState } from 'react';
import { TPartyFormFlow } from '../../../../types/party';
import {
	Box,
	Button,
	DialogActions,
	DialogContent,
	DialogTitle,
	Divider,
	Typography,
} from '@mui/material';
import { useBanPartyMember } from '../../../../hooks/useParties';
import { FormCommonButton, TextButton } from '../../../../styles/pages/party/forms/Form.styles';

type TPartyJoinFlowProps = {
	onFlowComplete: () => void;
	partyId: number;
	userId: number;
	userName: string;
};

export default function MemberBanFlow({
	onFlowComplete,
	partyId,
	userId,
	userName,
}: TPartyJoinFlowProps) {
	const [view, setView] = useState<TPartyFormFlow>('form');
	const { mutate: banPartyMember, isSuccess, isError } = useBanPartyMember({ partyId, userId });

	useEffect(() => {
		if (isSuccess) {
			setView('success');
		} else if (isError) {
			setView('failed');
		}
	}, [isSuccess, isError]);
	const handleOnBanClick = () => {
		// TODO:   api  
		banPartyMember({ partyId, userId });
	};
	switch (view) {
		case 'form':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent sx={{ paddingBottom: 0 }}>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							<span style={{ fontWeight: 'bold' }}>{userName}</span> 
							?
						</Typography>
						<Divider />
						<Box sx={{ padding: '5px 0' }}>
							<div> </div>
							<Typography
								sx={{ fontWeight: 'bold', fontSize: '18px' }}
							>{`${userName}`}</Typography>
						</Box>
					</DialogContent>
					<DialogActions>
						<TextButton onClick={onFlowComplete}></TextButton>
						<Button
							onClick={handleOnBanClick}
							color='error'
							variant='contained'
							disabled={false}
						>
							{''}
						</Button>
					</DialogActions>
				</>
			);
		case 'success':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${userName}  `}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</DialogActions>
				</>
			);
		case 'failed':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`${userName}   `}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</DialogActions>
				</>
			);
		default:
			return <></>;
	}
}
