import { useEffect, useState } from 'react';
import { useLeaveParty } from '../../../../hooks/useParties';
import { TPartyFormFlow } from '../../../../types/party';
import { DialogTitle, DialogContent, DialogActions, Button, Typography } from '@mui/material';
type TLeavePartyFlowData = {
	onFlowComplete: () => void;
	partyId: number;
	partyTitle: string;
};

export default function LeavePartyFlow({
	onFlowComplete,
	partyId,
	partyTitle,
}: TLeavePartyFlowData) {
	const { mutate: leaveParty, isSuccess, isError } = useLeaveParty(partyId); //   
	const [view, setView] = useState<TPartyFormFlow>('form');

	useEffect(() => {
		if (isSuccess) {
			setView('success');
		} else if (isError) {
			setView('failed');
		}
	}, [isSuccess, isError]);
	const handleOnLeaveClick = () => {
		leaveParty(partyId);
	};
	switch (view) {
		case 'form':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`'${partyTitle}'  ?`}
						</Typography>
					</DialogContent>
					<DialogActions>
						<Button onClick={onFlowComplete}></Button>
						<Button onClick={handleOnLeaveClick} variant='contained' disabled={false}>
							{''}
						</Button>
					</DialogActions>
				</>
			);
		case 'success':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`'${partyTitle}'  `}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<Button onClick={onFlowComplete} variant='contained' autoFocus>
							
						</Button>
					</DialogActions>
				</>
			);
		case 'failed':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`'${partyTitle}'   `}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<Button onClick={onFlowComplete} variant='contained' autoFocus>
							
						</Button>
					</DialogActions>
				</>
			);
		default:
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`      .`}
						</Typography>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`   .`}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<Button onClick={onFlowComplete} variant='contained' autoFocus>
							
						</Button>
					</DialogActions>
				</>
			);
	}
}
