import React, { useEffect, useState } from 'react';
import { useDisbandParty } from '../../../../hooks/useParties';
import { TPartyFormFlow } from '../../../../types/party';
import { Button } from '@mui/material';
import { DialogActions, DialogContent, DialogTitle, Typography } from '@mui/material';
import { TextButton, FormCommonButton } from '../../../../styles/pages/party/forms/Form.styles';

type TPartyDisbandFlowProps = {
	onFlowComplete: () => void;
	partyId: number;
};

export default function PartyDisbandFlow({ onFlowComplete, partyId }: TPartyDisbandFlowProps) {
	const [view, setView] = useState<TPartyFormFlow>('form');
	const { mutate: disbandParty, isSuccess, isError } = useDisbandParty(partyId);
	const handleOnClick = () => {
		disbandParty(partyId);
	};

	useEffect(() => {
		if (isSuccess) {
			setView('success');
		} else if (isError) {
			setView('failed');
		}
	}, [isSuccess, isError]);

	switch (view) {
		case 'form':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{` ?`}
						</Typography>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`     .`}
						</Typography>
					</DialogContent>
					<DialogActions>
						<TextButton onClick={onFlowComplete}></TextButton>
						<Button
							onClick={handleOnClick}
							variant='contained'
							color='error'
							disabled={false}
						>
							{' '}
						</Button>
					</DialogActions>
				</>
			);
		case 'success':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{` .`}
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</DialogActions>
				</>
			);
		case 'failed':
			return (
				<>
					<DialogTitle> </DialogTitle>
					<DialogContent>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							{`  .  .`}
						</Typography>
						<Typography sx={{ py: 4, textAlign: 'center' }}>
							   .
						</Typography>
					</DialogContent>
					<DialogActions>
						{/*        . */}
						<FormCommonButton onClick={onFlowComplete} variant='contained' autoFocus>
							
						</FormCommonButton>
					</DialogActions>
				</>
			);
	}
}
